.person .inner
{
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    .content
    {
        text-align: center;
    }
    div
    {
        margin: auto;
    }
    img 
    {
        position: absolute;
        max-width: 60px;
        top: calc(50% + 160px * var(--x) - 30px);
        left: calc(50% + 160px * var(--y) - 30px);
        animation: rotation 3s infinite linear;
        // animation-direction: alternate;
        
    }
}


